<template>

    <v-container fluid class="white text-xs-left">

        <v-layout>
            <v-flex xs12 md8 offset-md2>
                <h2 class="mt-3"><v-icon style="font-size: 25px">notifications</v-icon> Notificaes</h2>
                <v-card class="elevation-0 ma-1">
                    <v-flex xs12 offset-xs8 offset-sm10>
                        <v-btn round="" style="" small="" @click="$router.go(-1)" class="azul-principal white--text">Voltar</v-btn>
                    </v-flex>

                    <!--{{ userAuth }}-->
                    <template v-if="notifications.length > 0">

                        <template>
                            <p>Ol <b>{{ userAuth.name }}</b>, selecionamos alguns dados que precisam de actualizao urgente!</p>
                            <h3>O que precisa ser actualizado?</h3>

                            <template>
                                <br>
                                <div v-for="(notif, i) in notifications">{{ ++ i }}. {{ notif.text }}</div>

                                <br>
                                <!--<h4 v-if="userAuth.emailVerified || count > 1">Registar esses dados  simples, basta ir ao seu perfil registar onde as percentagens estao abaixo de 100%</h4>-->
                                <h4 v-if="true">Registar esses dados  simples, basta ir ao seu perfil</h4>
                                <h4 v-if="true">Actualize onde as percentagens esto abaixo de 100%</h4>
                                <br>
                            </template>
                            <v-flex xs12 v-if="isUserAuth">
                                <v-btn
                                        round=""
                                        style=""
                                        small=""
                                        :to="{name: 'Profile', params: { profile: userAuth.username }}"
                                        class="azul-principal white--text">
                                    <v-avatar size="25px">
                                        <img :src="userAuth.photoURL" alt="">
                                    </v-avatar>
                                    <span class="ma-1"></span> Ir ao meu Perfil
                                </v-btn>
                            </v-flex>
                            <br>
                            <br>
                        </template>

                        <template v-for="notif in notifications">
                            <v-alert :value="true" :color="notif.color === 'red' ? 'orange' : notif.color">
                                {{ notif.message }} <a style="color: white" :href="'#/' + userAuth.username">Ir ao meu perfil</a>
                            </v-alert>
                        </template>

                    </template>


                    <template v-else="">
                        <v-alert outline="" :value="true" type="success">
                            Os explicadores confiaveis que pode contactar, so os que te recomendamos! Contacte primeiro os recomendados, pois estes foram verificados!
                            <!--<a href="">Clique aqui para ve-los</a>-->
                        </v-alert>
                        <v-alert outline="" :value="true" type="info">
                            Ol, no deixe uma semana passar sem antes entrar para ver as novidades, estamos a trabalhar arduamente para melhorar a plataforma!
                        </v-alert>
                        <v-alert v-if="!isUserAuth" outline="" :value="true" type="success">
                            Registe-se para ver o que mais podes fazer na plaforma <b>Explicador</b>! <a :href="'#/register'">registar-me</a> | <a
                                :href="'#/login'">Entrar</a>
                        </v-alert>
                        <v-alert outline=""  :value="true" type="info">

                            <a target="_blank"
                               style="text-decoration-line: none;"
                               class="blue--text"
                               href="https://docs.google.com/forms/d/e/1FAIpQLSerGzBmhk9hdPUgC9awFRP6bdNETUtfo5rUG8MuywPmqtWIVA/viewform?usp=sf_link">
                                Acha que tem alguns dados como: Cursos, Escolas, Disciplinas,... em falta? pode clicar aqui!
                            </a>
                            <!---->
                        </v-alert>

                        <div>{{ emitirEvento(isUserAuth ? 3 : 4) }}</div>
                        <br>
                        <br>
                    </template>

                    <!--Gostaria de ser recomendado-->
                    <template v-if="isUserAuth && (userAuth.Explicador && userAuth.Explicador.subjectDetails.length && !userAuth.proAccount)">
                        <h2>Gostaria de ser recomendado?</h2>

                        <template>
                            <br>
                            <div>Os Explicadores por ns recomendados, devem ser realmente bons nas disciplinas que explicam e devem cumprir com os seguintes requisitos:</div>
                            <br>
                            <div>1. Ter uma foto de perfil definida</div>
                            <div>2. Ter todos dados preenchidos, incluindo a sua disponibilidade</div>
                            <div>3. Mandar um email para <b>info@exlicador.co.mz</b> informando que deseja ser recomendado, entraremos em contacto com ele!</div>

                            <br>
                            <h3>Depois de ser recomendado ficar assim:</h3>

                            <br>
                            <v-flex xs12 sm12 md10>
                                <v-flex xs10 sm4>
                                    <explicador
                                            :user="userAuth"
                                            :isProExample="true"
                                            :filterType="''"
                                            :filterName="''"
                                            :searchedData="''"
                                            :selectedNivel="''">
                                    </explicador>
                                </v-flex>
                            </v-flex>
                            <!--<div></div>-->
                            <!--<div>5. A sua disponibilidade </div>-->
                            <!--<div>6. E a sua localizacao</div>-->
                            <!--<div>7. Confirme o seu email, va ate ao fim da sua pagina do perfil para saber mais sobre confirmacao de email</div>-->
                        </template>

                        <br>
                        <br>
                    </template>

                </v-card>
            </v-flex>
        </v-layout>

    </v-container>

</template>

<script>
    import { eventBus } from '../../main'
    export default {
        beforeCreate() {

            this.$Progress.start()
            this.$Progress.finish()


        },
        mounted ()
        {
            this.scrollTo(0)
//            eventBus.$emit('mostrarTotalNofications', {total: notifications.length, color: 'red' }) //evento tratado no App.vue
        },
        data() {
            return {
//                showProcess: false,
                count: 0,
            }
        },
        computed: {
            error () {
                return this.$store.getters.error
            },
            loading () {
                return this.$store.getters.loading
            },
            isUserAuth () {
                return this.$store.getters.isUserAuth
            },
            userAuth () {
                return this.$store.getters.userAuth
            },

            users () {
                return this.$store.getters.getUsers
            },

            fakulUsers () {
                return this.$store.getters.getFakulUsers
            },
            notifications ()
            {
                let notifications = []
                if (!this.isUserAuth) return []

                if (!this.userAuth.identities) {
                    notifications.push({
                        message: 'Ainda no actualizou a sua identidade',
                        text: 'Sua identidade, se vo  \'Estudante\', \'Explicador\' ou \'Outro\'',
                        color: 'red',
                    })
                }
                if (!(this.userAuth.name && this.userAuth.telfone)) {
                    notifications.push({
                        message: 'Ainda no actualizou os seus dados pessoais',
                        text: 'Seus Dados pessoais',
                        color: 'red',
                    })
                }
                if (!(this.userAuth.Educations && this.userAuth.Educations.level)) {
                    notifications.push({
                        message: 'Ainda no actualizou os seus dados acadmicos',
                        text: 'Seus Dados Acadmicos',
                        color: 'red',
                    })
                }

                if (this.userAuth.identities && this.userAuth.identities.isExplicador)
                {
                    if (!(this.userAuth.Explicador && this.userAuth.Explicador.subjectDetails.length)) {
                        notifications.push({
                            message: 'Ainda no registou as disciplinas que explica',
                            text: 'As disciplinas que explicas',
                            color: 'red',
                        })
                    }

                }

                if ((this.userAuth.Explicador && this.userAuth.Explicador.subjectDetails.length))
                {
                    if (!(this.userAuth.Explicador && this.userAuth.Explicador.availability)) {
                        notifications.push({
                            message: 'Ainda no actualizou a sua disponibilidade',
                            text: 'A sua disponibilidade',
                            color: 'red',
                        })
                    }

                }

                if (!this.userAuth.Locations) {
                    notifications.push({
                        message: 'Ainda no actualizou a sua localizao',
                        text: 'A sua localizao',
                        color: 'red',
                    })
                }
                if (!this.userAuth.emailVerified) {
                    notifications.push({
                        message: 'Ainda no confirmou o seu email',
                        text: 'O seu email precisa ser confirmado, veja a opo de confirmao e saiba mais no fim da sua pgina de perfil.',
                        link: '',
                        color: 'red',
                    })
                }

                eventBus.$emit('mostrarTotalNofications', {total: notifications.length, color: 'red' }) //evento tratado no App.vue

                return notifications

            },




        },
        watch: {

            loading (value) {

//                if (!value && !this.error && this.showProcess) {
//
//                    let message = 'Parabens Nome, Voce acaba de activar a sua conta (Pro) assim para nos, voce eh um Explicador Profissional. ' +
//                        'Iremos te recomendar para as pessoas te procurarem. Se nao desejar mais ser profissional, eh  so entrar em contacto pelo: info@explicador.co.mz. Obrigado.'
//
//                    let text = 'As alteracoes para o User foram gravadas com sucesso!'
//                    let color = 'green'
//                    eventBus.$emit('showSnackbar', text, color); //Esse evento eh tratado no ficheiro App.vue, eh la onde foi montado o componente snackbar
//                    this.showProcess = false
//                    this.dateValid = false
//                    //Enviar email para o user.
//
//                }
            },
            notifications (value)
            {
                eventBus.$emit('mostrarTotalNofications', {total: value.length, color: 'red' }) //evento tratado no App.vue
            }
        },
        methods: {

            emitirEvento (total)
            {
                eventBus.$emit('mostrarTotalNofications', {total: total, color: 'blue' })
                return ''
            },
            scrollTo (el) {

                if (isNaN(el))
                {
//                    Scroll to a certain element
                    document.querySelector(el).scrollIntoView({
                        behavior: 'smooth'
                    });
                }
                else
                {
                    window.scroll({
                        top: el,
                        left: 10,
                        behavior: 'smooth'
                    });
                }




            },
        },
    }
</script>
<style>

    .nota1 {
        font-size: 30pt;
        color: darkblue;
        border-left: solid darkblue medium;
        padding-left: 15px;
    }
    .nota2 {
        font-size: 12pt;
        font-weight: bold;
        color: grey;
        border-left: solid darkblue medium;
        padding-left: 15px;
    }
    .explicador-text-info {
        font-weight: 200;
        font-size: 14pt;
        color: red;
    }
</style>


